/* NewsItem.css */

.card {
    border: 1px solid #ccc; /* Add a subtle border */
    border-radius: 8px; /* Rounded corners */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Soft shadow */
    transition: transform 0.3s ease; /* Smooth scaling effect on hover */
    height:450px;
    /* width:100%; */ 
}

.card:hover {
    transform: scale(1.03); /* Scale up on hover for a slight zoom effect */
}

.card img {
    max-height: 200px; /* Limit image height */
    object-fit: cover; /* Scale and crop images to fit */
    border-top-left-radius: 8px; /* Rounded corners for the image */
    border-top-right-radius: 8px;
}

.card-body {
    padding: 15px; /* Add padding inside the card body */
}

.card-title {
    font-size: 1.2rem; /* Larger font size for the title */
    margin-bottom: 10px; /* Spacing below the title */
}

.card-text {
    line-height: 1.5; /* Improve readability with slightly increased line height */
}

.btn-dark {
    background-color: #333; /* Dark background color */
    color: #fff; /* White text color */
    border: none; /* No border */
    padding: 10px 20px; /* Button padding */
    font-size: 0.9rem; /* Font size */
    border-radius: 5px; /* Rounded corners */
    text-decoration: none; /* Remove underline */
    display: inline-block; /* Inline-block display */
    cursor: pointer; /* Pointer cursor */
    transition: background-color 0.3s ease; /* Smooth transition for hover effect */
    position: absolute; /* Position the button absolutely */
    bottom: 10px; /* Distance from the bottom */
    
    
}

.btn-dark:hover {
    background-color: #555; /* Darker shade on hover */
}
